export const phaseFunc = (num) => {
  sec7arr.map((v, i) => {
    v.map((value, index) => {
      if (num < 30) {
        num += 5;
      } else if (num >= 30) {
        //  30  +10 
        num += 10;
      }
      value.percent = num;
    });
  });
  return sec7arr;
};
